import React, { useState, useEffect } from 'react';
import classNames from 'classnames';
import { SectionTilesProps } from '../../utils/SectionProps';
import SectionHeader from './partials/SectionHeader';

import Modal from '../elements/Modal';

const propTypes = {
  ...SectionTilesProps.types,
};

const defaultProps = {
  ...SectionTilesProps.defaults,
};

const Savings = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {
  const outerClasses = classNames(
    'testimonial section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'testimonial-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const tilesClasses = classNames('tiles-wrap', pushLeft && 'push-left');

  const sectionHeader = {
    title: 'Save over 5+ Lakhs in bills',
    // paragraph: 'Vitae aliquet nec ullamcorper sit amet risus nullam eget felis semper quis lectus nulla at volutpat diam ut venenatis tellusin ornare.'
  };

  const [showModal, setShowModal] = useState('');

  const openModal = (e) => {
    e.preventDefault();

    let id = e.target.closest('.tiles-item').dataset.modal;

    console.log('aaa', id);
    setShowModal(id);
  };

  const closeModal = (e) => {
    e.preventDefault();
    setShowModal('');
  };

  return (
    <section {...props} className={outerClasses} id="savings">
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>
            <div
              className="tiles-item reveal-from-right"
              data-reveal-delay="200"
              data-modal="savings1"
              onClick={openModal}
            >
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                    - Save over 10k in hormonal pills
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-bottom"
              data-modal="savings2"
              onClick={openModal}
            >
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                     Save over 25k in gyms and yoga classes fees
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-left"
              data-reveal-delay="200"
              data-modal="savings3"
              onClick={openModal}
            >
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                     Save over 10k in diet plans (also +Rs 5,000/month you
                    spend buying new diet ingredients)
                  </p>
                </div>
              </div>
            </div>

            <div
              className="tiles-item reveal-from-top"
              data-modal="savings4"
              onClick={openModal}
            >
              <div className="tiles-item-inner">
                <div className="testimonial-item-content">
                  <p className="text-sm mb-0">
                     Save over 5000 in doctor consultations (with over 10,000
                    in psychologists consultations)
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Modal */}
      {showModal === 'savings1' && (
        <Modal id="savings1" show={!!showModal} handleClose={closeModal}>
          Stats For savings 1
        </Modal>
      )}

      {showModal === 'savings2' && (
        <Modal id="savings2" show={!!showModal} handleClose={closeModal}>
          Stats For savings 2
        </Modal>
      )}

      {showModal === 'savings3' && (
        <Modal id="savings3" show={!!showModal} handleClose={closeModal}>
          Stats For savings 3
        </Modal>
      )}

      {showModal === 'savings4' && (
        <Modal id="savings4" show={!!showModal} handleClose={closeModal}>
          Stats For savings 4
        </Modal>
      )}
    </section>
  );
};

Savings.propTypes = propTypes;
Savings.defaultProps = defaultProps;

export default Savings;
