import React from "react";
import classNames from "classnames";
import { SectionTilesProps } from "../../utils/SectionProps";
import SectionHeader from "./partials/SectionHeader";

const propTypes = {
  ...SectionTilesProps.types,
};

const defaultProps = {
  ...SectionTilesProps.defaults,
};
const FeaturedIn = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {
  const outerClasses = classNames(
    "faq section",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "faq-inner section-inner",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  const sectionHeader = {
    title: "Featured In",
    // paragraph:
    //   "We surround participants with the human support and digital tools they need to succeed.",
  };

  return (
    <section {...props} className={outerClasses} id="featured-in">
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className="featured-in-wrapper">
            <div className="featured-in-item">
              <img
                src="/assets/images/dainik-jagran.png"
                alt="Featured in Dainik Jagran"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

FeaturedIn.propTypes = propTypes;
FeaturedIn.defaultProps = defaultProps;

export default FeaturedIn;
