import React, { Fragment, useState, useEffect } from 'react';
import SectionHeader from '../components/sections/partials/SectionHeader';
import amplitude from 'amplitude-js';

const PaytmPayment = () => {
  amplitude.getInstance().logEvent('ON PAYMENT PAGE');

  const [formData, setFormData] = useState({
    fullName: '',
    phoneNumber: '',
    email: '',
  });
  const { fullName, phoneNumber, email } = formData;
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    makePayment(fullName, email, phoneNumber);
  };

  function isDate(val) {
    // Cross realm comptatible
    return Object.prototype.toString.call(val) === '[object Date]';
  }

  function isObj(val) {
    return typeof val === 'object';
  }

  function stringifyValue(val) {
    if (isObj(val) && !isDate(val)) {
      return JSON.stringify(val);
    } else {
      return val;
    }
  }

  function buildForm({ action, params }) {
    const form = document.createElement('form');
    form.setAttribute('method', 'post');
    form.setAttribute('action', action);
    Object.keys(params).forEach((key) => {
      const input = document.createElement('input');
      input.setAttribute('type', 'hidden');
      input.setAttribute('name', key);
      input.setAttribute('value', stringifyValue(params[key]));
      form.appendChild(input);
    });
    return form;
  }

  function post(details) {
    const form = buildForm(details);
    document.body.appendChild(form);
    form.submit();
    form.remove();
  }

  const getData = (data) => {
    return fetch(`https://7c-website-backend.azurewebsites.net/api/payment`, {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .catch((err) => console.log(err));
  };
  const makePayment = (fullName, email, phoneNumber) => {
    getData({
      fullName: fullName,
      email: email,
      phoneNumber: phoneNumber,
    }).then((response) => {
      var information = {
        // action: 'https://securegw-stage.paytm.in/order/process',
        action: 'https://securegw.paytm.in/order/process',
        params: response,
      };
      // console.log(information);
      post(information);
    });
  };

  return (
    <>
      <SectionHeader
        tag="h3"
        data={{ title: 'Join our community' }}
        className="center-content"
        titleClass="text-color-secondary"
        style={{ marginTop: '116px' }}
      />
      {/* <div className="container"> */}
      <div className="row my-5">
        <div className="col-md-4 offset-md-4">
          <div className="card">
            <div className="card-body">
              <form onSubmit={(e) => onSubmit(e)}>
                <div className="form-group">
                  <input
                    placeholder="Your full name"
                    className="form-control"
                    type="text"
                    required={true}
                    name="fullName"
                    value={fullName}
                    onChange={(e) => onChange(e)}
                  />
                </div>
                <div className="form-group">
                  <input
                    placeholder="Enter your email id"
                    className="form-control"
                    type="email"
                    required={true}
                    name="email"
                    value={email}
                    onChange={(e) => onChange(e)}
                  />
                </div>
                <div className="form-group">
                  <input
                    placeholder="Enter mobile number"
                    className="form-control"
                    type="text"
                    required={true}
                    name="phoneNumber"
                    minLength="10"
                    value={phoneNumber}
                    onChange={(e) => onChange(e)}
                  />
                </div>
                <div className="form-group">
                  <button
                    // style={{ backgroundColor: '#6163FF' }}
                    type="submit"
                    className="btn form-control btn-primary"
                  >
                    Pay Now (Rs.299/Month)
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      {/* </div> */}
    </>
  );
};

export default PaytmPayment;
