import React, { useState, useEffect } from 'react';
import classNames from 'classnames';
import { SectionTilesProps } from '../../utils/SectionProps';
import SectionHeader from './partials/SectionHeader';
import Image from '../elements/Image';
import Tst01Video from '../../assets/videos/testimonial01.mp4';
import Tst02Video from '../../assets/videos/testimonial02.mp4';
import Tst03Video from '../../assets/videos/testimonial03.mp4';

import Tst01Image from '../../assets/images/test01Image.png';
import Tst02Image from '../../assets/images/test02Image.png';
import Tst03Image from '../../assets/images/test03Image.png';

import Card from '../elements/Card';
import Modal from '../elements/Modal';

const propTypes = {
  ...SectionTilesProps.types,
};

const defaultProps = {
  ...SectionTilesProps.defaults,
};
const UserTestimonials = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {
  const outerClasses = classNames(
    'user-testimonials-tiles section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'user-testimonials-tiles-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const tilesClasses = classNames(
    'tiles-wrap center-content',
    pushLeft && 'push-left'
  );

  const sectionHeader = {
    title: 'Little changes, big results',
    // paragraph:
    //   "We surround participants with the human support and digital tools they need to succeed.",
  };

  const [showModal, setShowModal] = useState('');

  const openModal = (e) => {
    e.preventDefault();

    let id = e.target.closest('.user-testimonials-tiles-item-content').dataset
      .modal;

    console.log('aaa', id);
    setShowModal(id);
  };

  const closeModal = (e) => {
    e.preventDefault();
    setShowModal('');
  };

  return (
    <section {...props} className={outerClasses} id="digital-care">
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>
            <div className="tiles-item reveal-from-bottom">
              <Card
                className="user-testimonial-card"
                style={{ cursor: 'pointer' }}
              >
                <div
                  className="user-testimonials-tiles-item-content"
                  data-modal="testimonial1"
                  onClick={openModal}
                >
                  <img src={Tst01Image} />
                </div>
              </Card>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <Card
                className="user-testimonial-card"
                style={{ cursor: 'pointer' }}
              >
                <div
                  className="user-testimonials-tiles-item-content"
                  data-modal="testimonial2"
                  onClick={openModal}
                >
                  <Image src={Tst02Image} />
                </div>
              </Card>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <Card
                className="user-testimonial-card"
                style={{ cursor: 'pointer' }}
              >
                <div
                  className="user-testimonials-tiles-item-content"
                  data-modal="testimonial3"
                  onClick={openModal}
                >
                  <Image src={Tst03Image} />
                </div>
              </Card>
            </div>
          </div>
        </div>
      </div>

      {/* Modal */}
      {showModal === 'testimonial1' && (
        <Modal id="testimonial1" show={!!showModal} handleClose={closeModal}>
          <video src={Tst01Video} controls autoPlay></video>
        </Modal>
      )}

      {showModal === 'testimonial2' && (
        <Modal id="testimonial2" show={!!showModal} handleClose={closeModal}>
          <video src={Tst02Video} controls autoPlay></video>
        </Modal>
      )}
      {showModal === 'testimonial3' && (
        <Modal id="testimonial3" show={!!showModal} handleClose={closeModal}>
          <video src={Tst03Video} controls autoPlay></video>
        </Modal>
      )}
    </section>
  );
};

UserTestimonials.propTypes = propTypes;
UserTestimonials.defaultProps = defaultProps;

export default UserTestimonials;
